// src/app/app/page.tsx
"use client";

import { useSession } from "next-auth/react";
import { useTier } from "@/lib/db/useTier";

// Import the new hub components
import FreeUserHub from "@/components/app/FreeUserHub";
import ProUserHub from "@/components/app/ProUserHub";

// Loading Skeleton Component
function HubSkeleton() {
  return (
    <div className="space-y-10 animate-pulse">
      <div className="h-10 w-2/3 bg-gray-200 rounded-lg"></div>
      <div className="h-40 w-full bg-gray-200 rounded-lg"></div>
      <div className="h-8 w-1/3 bg-gray-200 rounded-lg"></div>
      <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
        <div className="h-36 bg-gray-200 rounded-lg"></div>
        <div className="h-36 bg-gray-200 rounded-lg"></div>
        <div className="h-36 bg-gray-200 rounded-lg"></div>
        <div className="h-36 bg-gray-200 rounded-lg"></div>
      </div>
    </div>
  );
}

// Main Page Component
export default function AppHomePage() {
  const { data: session } = useSession();
  const tier = useTier();
  const isFree = tier === "free";

  // Show loading skeleton while tier is being determined
  if (!tier) {
    return <HubSkeleton />;
  }

  return (
    <div className="space-y-10">
      {/* Welcome Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">
          Welcome back, {session?.user?.name?.split(" ")[0] || "User"}!
        </h1>
      </div>

      {/* Render different content based on tier */}
      {isFree ? <FreeUserHub /> : <ProUserHub />}
    </div>
  );
}
