// src/components/app/ProUserHub.tsx
"use client";

import Link from "next/link";
import {
  ListBulletIcon,
  PencilIcon,
  BookOpenIcon,
  ClockIcon,
} from "@heroicons/react/24/outline";
import { useTier } from "@/lib/db/useTier";
import { motion } from "framer-motion";

export default function ProUserHub() {
  const tier = useTier();

  const quickLinks = [
    {
      name: "My Tasks",
      href: "/app/tasks",
      icon: ListBulletIcon,
      description: "View all your to-dos",
    },
    {
      name: "My Notes",
      href: "/app/notes",
      icon: PencilIcon,
      description: "Capture a new idea",
    },
    {
      name: "My Diary",
      href: "/app/diary",
      icon: BookOpenIcon,
      description: "Write today's entry",
    },
    {
      name: "Focus Timer",
      href: "/app/pomodoro",
      icon: ClockIcon,
      description: "Start a focus session",
    },
  ];

  const itemVariant = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 },
  };

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.07, // Stagger links
      },
    },
  };

  return (
    <div className="space-y-10">
      {/* Tier Status Card */}
      <div className="rounded-lg border border-green-200 bg-gradient-to-r from-green-50 to-emerald-50 p-6 shadow-sm">
        <h3 className="text-xl font-semibold text-green-900">
          Welcome to TaskGlyph{" "}
          {tier ? `${tier.charAt(0).toUpperCase() + tier.slice(1)}` : ""}!
        </h3>
        <p className="mt-1 text-green-700">
          You have unlimited access to all features. Thank you for your support!
        </p>
      </div>

      {/* Quick Links Grid (Useful for paid users) */}
      <div>
        <h2 className="text-xl font-semibold text-gray-900 mb-4">
          Quick Access
        </h2>
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate="visible"
          className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4"
        >
          {quickLinks.map((link) => (
            <motion.div
              key={link.name}
              variants={itemVariant}
              whileHover={{ scale: 1.05 }}
              transition={{ type: "spring", stiffness: 400 }}
            >
              <Link
                href={link.href}
                className="group relative block p-6 bg-white rounded-lg shadow-md border border-gray-200 hover:shadow-xl hover:border-blue-500 transition-all duration-300"
              >
                <span className="p-3 bg-gray-100 text-gray-700 rounded-lg inline-flex group-hover:bg-blue-100 group-hover:text-blue-600 transition-colors">
                  <link.icon className="h-6 w-6" />
                </span>
                <h3 className="mt-4 text-lg font-semibold text-gray-900">
                  {link.name}
                </h3>
                <p className="mt-1 text-sm text-gray-500">{link.description}</p>
              </Link>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </div>
  );
}
